
<template>
  <div>
    <!-- Nibbles,Tuffy,Magnolia,Leonhart,Krista,Toodles -->
    <h2>Date: {{ date }}</h2>
    <h2>Week: {{ week }}</h2>
    <button @click="clear">Clear all</button> &nbsp;
    <button @click="copydata">Copy data</button>
    &nbsp;
    <button @click="restore">Restore data</button>
    <table>
      <thead>
        <tr>
          <th></th>
          <th v-for="(a, i) in accounts" :key="i">
            {{ a }}
          </th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(e, eid) in events" :key="eid">
          <td>{{ e }}</td>
          <th
            v-for="(a, aid) in accounts"
            :key="aid"
            @click="toggle(eid, a, isDone(eid, a))"
            :class="{ done: isDone(eid, a) }"
          >
            <span v-if="isDone(eid, a)" style="color: green">Done</span>
          </th>
        </tr>
      </tbody>
    </table>

    <h3>Weekly events</h3>
    <table>
      <thead>
        <tr>
          <th></th>
          <th v-for="(a, i) in accounts" :key="i">
            {{ a }}
          </th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(e, eid) in weekEvents" :key="eid">
          <td>{{ e }}</td>
          <th
            v-for="(a, aid) in accounts"
            :key="aid"
            @click="toggleWeek(eid, a, isDoneWeek(eid, a))"
            :class="{ done: isDoneWeek(eid, a) }"
          >
            <span v-if="isDoneWeek(eid, a)" style="color: green">Done</span>
          </th>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import dataMixin from "../dataMixin";
export default {
  mixins: [dataMixin],
  created() {
    this.load();
    console.log(this.date);
  },
  methods: {
    toggle(eid, acc, checked) {
      const tracker = this.trackers.find((t) => t.acc === acc);
      console.log(eid, tracker);
      if (!tracker) {
        this.trackers.push({ acc, events: [eid] });
        return;
      }

      if (!checked) {
        !tracker.events.includes(eid) && tracker.events.push(eid);
      } else {
        tracker.events = tracker.events.filter((i) => i !== eid);
      }
    },
    isDone(eid, acc) {
      const tracker = this.trackers.find((t) => t.acc === acc);
      return tracker && tracker.events.includes(eid);
    },
    toggleWeek(eid, acc, checked) {
      const tracker = this.weekTrackers.find((t) => t.acc === acc);
      console.log(eid, tracker);
      if (!tracker) {
        this.weekTrackers.push({ acc, events: [eid] });
        return;
      }

      if (!checked) {
        !tracker.events.includes(eid) && tracker.events.push(eid);
      } else {
        tracker.events = tracker.events.filter((i) => i !== eid);
      }
    },
    isDoneWeek(eid, acc) {
      const tracker = this.weekTrackers.find((t) => t.acc === acc);
      return tracker && tracker.events.includes(eid);
    },
  },
  watch: {
    trackers: {
      deep: true,
      handler: function (newVal) {
        localStorage.setItem("trackers-" + this.date, JSON.stringify(newVal));
      },
    },
    weekTrackers: {
      deep: true,
      handler: function (newVal) {
        localStorage.setItem("w-trackers-" + this.week, JSON.stringify(newVal));
      },
    },
  },
};
</script>

<style scoped>
table {
  width: 100%;
  border-spacing: 0;
}

td:first-child {
  width: 1px;
  white-space: nowrap;
}

th,
td {
  padding: 10px;
  border: 1px solid lightgrey;
  user-select: none;
  max-height: 25px !important;
}

.done {
  background-color: yellowgreen;
}

.copydt {
  margin: 5px;
}
</style>