import { currentWeek, todayYmd } from "./util";

export default {
  methods: {
    load() {
      const s = localStorage.getItem("accounts");
      if (!s) {
        const as = window.prompt("Accounts: ");
        this.accounts = as.split(",");
        localStorage.setItem("accounts", JSON.stringify(this.accounts));
      } else {
        this.accounts = JSON.parse(s);
      }
      this.date = todayYmd();
      this.week = currentWeek(new Date());
      const t = localStorage.getItem("trackers-" + this.date) ?? "[]";
      this.trackers = JSON.parse(t);
      const wt = localStorage.getItem("w-trackers-" + this.week) ?? "[]";
      this.weekTrackers = JSON.parse(wt);
    },
    clear() {
      localStorage.removeItem("accounts");
      window.location.reload();
    },

    copydata() {
      const accounts = localStorage.getItem("accounts");
      const dayTracking = localStorage.getItem("trackers-" + this.date);
      const weekTracking = localStorage.getItem("w-trackers-" + this.week) ?? "[]";
      const s = JSON.stringify({ accounts, dayTracking, weekTracking });
      console.log(accounts, dayTracking, weekTracking);
      window.navigator.clipboard
        .writeText(s)
        .then(() => alert("Done: " + s))
        .catch(e => alert(e));
    },
    restore() {
      const s = window.prompt("Your data?");
      if (!s || !s.trim()) {
        return alert("Cancel");
      }
      const data = JSON.parse(s);
      console.log(data);
      localStorage.setItem("accounts", data.accounts);
      localStorage.setItem("trackers-" + this.date, data.dayTracking);
      localStorage.setItem("w-trackers-" + this.week, data.weekTracking);
      alert("Done");
      window.location.reload();
    },
  },
  mounted() {
    console.log(this.accounts);
  },
  data() {
    return {
      accounts: [],
      events: [
        "Điểm danh",
        "Cầu phúc",
        "Góp quân tư",
        "Thần thú",
        "Thí luyện AH",
        "Full phó bản",
        "Full Q",
        "Treo thưởng",
        "Gia viên nuôi trồng",
      ],
      weekEvents: [
        "Hiệp khách",
        "Cầu nguyện",
        "Quà khởi công",
        "Trống chinh chiến",
        "Đổi rương",
        "Nuôi thủ hộ thú",
        "Ôn tuyền lần 1",
        "Ôn tuyền lần 2",
        "Ôn tuyền lần 3",
      ],
      date: null,
      week: null,
      trackers: [],
      weekTrackers: [],
    };
  },
  watch: {},
};
